.Content {
  margin: 0 auto;
  height: auto;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

#imgGear1 {
  position: fixed;
  width: 250px;
  height: auto;
  bottom: -100px;
  left: -50px;
  transform: rotate(var(--scrollRotate--));
}

#imgGear2 {
  position: fixed;
  width: 100px;
  height: auto;
  bottom: 80px;
  left: 160px;
  transform: rotate(calc(var(--scrollRotate--) * -1.2));
}

#imgGear3 {
  position: fixed;
  width: 150px;
  height: auto;
  top: -5%;
  right: 0px;
}

#Techin {
  position: relative;
  margin: 0 auto;
  padding: 40px;
  padding-top: calc(65px + 40px);
  width: 100%;
  background-color: #6C9BCF;
  position: relative;
  z-index: 0;
}

#Skills {
  position: relative;
  width: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 65px;
  padding-bottom: 65px;
  width: 100%;
  background-color: #6C9BCF;
}

#Projects {
  position: relative;
  width: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 65px;
  padding-bottom: 65px;
  width: 100%;
  background-color: #6C9BCF;
}

#Contact {
  position: relative;
  width: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 65px;
  padding-bottom: 65px;
  width: 100%;
  background-color: #6C9BCF;
}

#scrollToTop {
  position: fixed;
  right: 10%;
  bottom: 70px;
  width: 0px;
  height: 0px;
  z-index: 200;
  opacity: 0.4;
  transition: opacity 1s;
  animation: fadeIn 0.75s ease;
}

#scrollToTop:hover {
  opacity: 1;
}

.HideScrollUp {
  opacity: 0.2;
}

#imgArrowScrollTop {
  position: absolute;
  top: 0;
  width: 0;
  height: 0;
}

#imgArrowScrollTop:hover {
  animation: Updown 0.5s ease infinite;
  opacity: 1;
  cursor: pointer;
}

#imgGearScrollTop {
  position: absolute;
  width: 70%;
  height: 70%;
}

#imgGear4 {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 200px;
}

.imgGear {
  position: absolute;
  width: 0;
  height: 0;
}


@media screen and (max-width: 430px) {}

@media screen and (max-width: 600px) {
  #Skills {
    z-index: -1;
  }

  #Techin {
    padding: 0;
    padding-top: 40px;
  }
}

@media screen and (width: 600px) {
  #Techin {
    padding: 0;
    padding-top: 100px;
  }
}

@media screen and (min-width: 600px) {
  #scrollToTop {
    right: 5px;
    bottom: 70px;
    width: 50px;
    height: 65px;
  }

  #imgArrowScrollTop {
    position: absolute;
    top: 0;
    width: 30px;
    height: 30px;
    left: 50%;
    transform: translate(-50%, -0%);
    padding-bottom: 50px;
    z-index: 201;
  }

  #imgGearScrollTop {
    position: absolute;
    width: 35px;
    height: 35px;
    right: 7px;
    bottom: 0;
    transform: rotate(var(--scrollRotate--));
  }

  #imgGearScrollTop:hover {
    cursor: pointer;
  }

}

