@keyframes Updown {
  0% {
    top: 0px;
    opacity: 1;
  }

  50% {
    top: -10px;
    opacity: 0.9;
  }

  100% {
    top: -0px;
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    transform: translate3d(0, -20%, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes rotateGear {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes ReRotateGear {
  0% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(0);
  }
}